html {
  height: 100%;
  font-family: Segoe UI;
  background-color: #F6F5F5;
}

.log-reg {
  width: 900px;
  height: 500px;
  background-color: #EEF6FF;
  margin: calc(50vh - 250px) auto;
  border-radius: 0 50px 0 0;
  box-shadow: 0 3px 6px #00000016;
  display: flex;
  flex-direction: row;
}

.left, .right {
  width: 50%;
  padding: 85px 65px 0 65px;
  display: flex;
  flex-direction: column;
  border-radius: 0 50px 0 0;
}

.right {
  background-color: #FFFFFF;
}

@keyframes slide {
  from {
    width: 450px;
    margin-left: calc(50vw - 450px);
  }
  to {
    width: 450px;
    margin-left: 50vw;
  }
}

@-webkit-keyframes slide {
  from {
    width: 900px;
  }
  to {
    width: 450px;
  }
}

.OR {
  position: absolute;
  width: 40px;
  height: 40px;
  top: calc(50vh + 148px);
  left: max(calc(50vw - 20px), 430px);
  border-radius: 40px;
  background-color: #B5DAE3;
  line-height: 40px;
  font-size: 20px;
  text-align: center;
}

.title {
  font-size: 40px;
}

.form-container {
  height: 240px;
  display: flex;
  flex-direction: column;
}

.form-button {
  height: 60px;
  position: relative;
  display: flex;
  flex-direction: row;
}

.input-form {
  display: flex;
  flex-direction: column;
  flex: auto;
  justify-content: center;
}

.input-form label{
  font-size: 18px;
}

.input-form input{
  line-height: 40px;
  height: 40px;
  width: 100%;
  padding: 0px 10px;
  border-color: #707070;
  border-width: 1px;
  box-sizing: border-box;
}

.form-button label {
  height: 20px;
  width: 100%;
  line-height: 20px;
  font-size: 14px;
  text-align: right;
}

.form-button button {
  height: 40px;
  width: 100%;
  background-color: #7DAFBC;
  font-size: 24px;
  color: #FFFFFF;
  border: 0px;
  position: absolute;
  bottom: 0px;
}

.form-button button:hover {
  background-color: #8CCADA;
}

.form-button button:active {
  background-color: #548D9C;
}

@keyframes background {
  from {
    top: -15vh;
    left: -10vw;
  }
  to {
    top: 0px;
    left: 0px;
  }
}

@-webkit-keyframes background {
  from {
    top: -10vh;
  }
  to {
    top: 0px;
  }
}

.background {
  position: absolute;
  top: 0px;
}

.background::after {
  content: "";
  background-color: transparent;
  position: absolute;
  border-style: solid;
  box-sizing: border-box;
}

.background::before {
  content: "";
  background-color: transparent;
  position: absolute;
  border-style: solid;
  box-sizing: border-box;
}

.background-1 {
  background-color: #D3E0EA;
  width: 95vw;
  height: 60vh;
  z-index: -10;
  animation: background 0.5s;
}

.background-1::after {
  left: 95vw;
  border-width: 60vh 5vw 0px 0px;
  border-color: #D3E0EA transparent transparent transparent;
}

.background-1::before {
  top: 60vh;
  border-width: 15vh 95vw 0px 0px;
  border-color: #D3E0EA transparent transparent transparent;
}


.background-2 {
  background-color: #1687A7;
  width: 90vw;
  height: 52vh;
  z-index: -9;
  animation: background 1s;
}

.background-2::after {
  left: 90vw;
  border-width: 52vh 10vw 0px 0px;
  border-color: #1687A7 transparent transparent transparent;
}

.background-2::before {
  top: 52vh;
  border-width: 23vh 90vw 0px 0px;
  border-color: #1687A7 transparent transparent transparent;
}

.background-3 {
  background-color: #276678;
  width: 85vw;
  height: 45vh;
  z-index: -8;
  animation: background 1.5s;
}

.background-3::after {
    left: 85vw;
    border-width: 45vh 15vw 0px 0px;
    border-color: #276678 transparent transparent transparent;
}

.background-3::before {
    top: 45vh;
    border-width: 30vh 85vw 0px 0px;
    border-color: #276678 transparent transparent transparent;
}
